.hero-img{
    width: 100%;
    background-color: rgb(0, 0, 0);
    height: 60vh;
    position: relative;
}
.hero-img::before{
    content: "";
   
    background-size: cover;
    background-position: center top;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}
.heading{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hero-img h1{
    
    font-size: 3rem;
}

.hero-img p{
    font-size: 1.4rem;
    text-align: center;
    padding: auto;
    margin-left: 10rem;
    margin-right: 10rem;
}

@media screen and (max-width:640px) 
{
    .hero-img h1{
       padding-top: 20%;
        
        font-size: 1.4rem;
    }
    .hero-img p{
        font-size: 1.4rem;
        text-align: center;
        margin-left: 2rem;
        margin-right: 2rem;
        

    }
    
}